
.container {
    max-width: 960px;
    margin: 0 auto;
    padding: 0 16px;
    margin-bottom: 16px;
}
header {
    display: flex;
    align-items: center;
        justify-content: space-between;
        gap: 12px;
        padding: 8px 0;

        border-bottom: 1px solid black;
}

.navigation {
    display: flex;
    margin: 0;
    padding: 0;
    li {
        list-style: none;
        padding: 8px 16px;
        a {
            padding: 8px 16px;
            border-radius: 4px;
            text-decoration: none;
            color: black;
            font-weight: 500;

            &:active {
                color: white;
                background-color: orangered;
            }
        }
    }
}



